import React from 'react';

export default function Vaihtonappi({tila, func2}) {
    if (tila.vertailu === "aine") {
        return(
            <div className='row'>
                <button className = 'vaihdaVertailu l valittu' type = 'button' onClick = {() => func2("aine")}>Vuodet</button>
                <button className = 'vaihdaVertailu r' type = 'button' onClick = {() => func2("aika")}>Aineet</button>
            </div>)
    } else if (tila.vertailu === "aika") {
        return(
            <div className='row'>
                <button className = 'vaihdaVertailu l' type = 'button' onClick = {() => func2("aine")}>Vuodet</button>
                <button className = 'vaihdaVertailu r valittu' type = 'button' onClick = {() => func2("aika")}>Aineet</button>
            </div>)
    }
}