import {useState} from 'react';
import React from 'react';
import Sidebar from './sidebar.js'
import Taulukko from './taulukko.js'
import '../styles/app.css'

export default function App({data}) {
    let [tila, asetaTila] = useState({
        vertailu: "aine",
        kohde: "syksy-2021"
    })
    let vakioTila = {
        aine: "syksy-2021",
        aika: "Äidinkieli, suomi"
    }
    let otsikko = ""
    if (tila.vertailu === "aine") {
        otsikko = tila.kohde.charAt(0).toUpperCase() + tila.kohde.replace("-", " ").replace("a", "ä").slice(1)
    } else {
        otsikko = tila.kohde
    }
    const paivita = (uusiTila) => {
        asetaTila(() => {
            return {...tila, kohde: uusiTila}
        })
    }
    const paivitaVertailu = (uusiVertailu) => {
        asetaTila(() => {
            return {vertailu: uusiVertailu, kohde: vakioTila[uusiVertailu]}
        })
    }
    return(
        <div>
            <header className = 'otsikko'>
                <h1>{otsikko}</h1>
            </header>
            <div className='row tablecontainer'>
                <Taulukko data = {data} tila = {tila}/>
                <Sidebar data = {data} tila = {tila} func1 = {paivita} func2 = {paivitaVertailu}/>
            </div>
        </div>
    )
}
